/*Fonts*/
@import url("https://fonts.googleapis.com/css2?family=Caveat:wght@500&family=Poppins:ital,wght@0,100;0,200;1,200&display=swap");
/*Profiles*/
#root > div {
  --primary: white;
  --secondary: black;
  --tertiary: red;
}

/*General Styling*/
body {
  background: black;
  color: white;
  min-height: 100vh;
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}